import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    { path: '/', name: '/', component:()=>import("@/page/Home") },
    { path: '/Page1', name: '/Page1', component:()=>import("@/page/Page1") },
    { path: '/Page2', name: '/Page2', component:()=>import("@/page/Page2") },
    { path: '/Page3', name: '/Page3', component:()=>import("@/page/Page3") },
    { path: '/Page4', name: '/Page4', component:()=>import("@/page/Page4") },
    { path: '/Page5', name: '/Page5', component:()=>import("@/page/Page5") },
  ] 
})
